import {
	orderDetail,
	getRefundReason,
	postOrderRefund
} from "@/api/order";
import {
	trim
} from "@/utils";
import {
	VUE_APP_API_URL
} from "@/config";

export default {
	name: "goodsReturn",
	components: {
		// VueCoreImageUpload
	},
	data() {
		return {
			url: `${VUE_APP_API_URL}/upload/image`,
			headers: {
				Authorization: "Bearer " + this.$store.state.token
			},
			id: 0,
			orderInfo: {},
			reasonList: [],
			reason: "",
			refund_reason_wap_explain: "",
			refund_reason_wap_img: []
		};
	},
	methods: {
		changeReason(e) {
			this.reason = this.reasonList[e.mp.detail.value];
		},
		imageuploaded(res) {
			if (res.status !== 200) {
				uni.showToast({
					title: err.msg || err.response.data.msg,
					icon: 'none',
					duration: 2000
				});
				return
			}
			this.refund_reason_wap_img.push(res.data.url);
		},
		getOrderDetail() {
			orderDetail(this.id)
				.then(res => {
					this.orderInfo = res.data;
				})
				.catch(err => {
					uni.showToast({
						title: err.msg || err.response.data.msg,
						icon: 'none',
						duration: 2000
					});
				});
		},
		getRefundReason() {
			getRefundReason().then(res => {
				this.reasonList = res.data;
			});
		},
		submit() {
			const refund_reason_wap_explain = trim(this.refund_reason_wap_explain),
				text = this.reason;
			if (!text) {
				uni.showToast({
					title: "请选择退款原因",
					icon: 'none',
					duration: 2000
				});
				return
			}
			postOrderRefund({
					text,
					uni: this.orderInfo.orderId,
					refund_reason_wap_img: this.refund_reason_wap_img.join(","),
					refund_reason_wap_explain
				})
				.then(res => {
					uni.showToast({
						title: res.msg,
						icon: "success",
						duration: 2000
					});
					setTimeout(() => {
						this.$yrouter.back();
					}, 1500);
				})
				.catch(err => {
					uni.showToast({
						title: err.msg || err.response.data.msg,
						icon: 'none',
						duration: 2000
					});
				});
		}
	},
	mounted() {
		this.id = this.$yroute.query.id || 0;
		this.getOrderDetail();
		this.getRefundReason();
	}
};
